const SetApp = r => require.ensure([], () => r(require('../page/apply/setApp.vue')), 'SetApp');
const StopUseApp = r => require.ensure([], () => r(require('../page/apply/stopUseApp/stopUseApp.vue')), 'StopUseApp');
